import { Inline, InlineProperties, Data } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface MentionData extends Data {
  readonly id?: string;
  readonly name?: string;
  readonly login?: string;
  readonly isPlaceholder?: boolean;
}

export interface MentionProperties {
  data?: MentionData;
  nodes?: InlineProperties['nodes'];
}

export default class Mention extends Inline {
  static TYPE = ELEMENT_TYPE;

  static create(properties: Mention | MentionProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`Mention.create` does not accept string');
    }
    if (Mention.isMention(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: Mention.TYPE,
    }) as Mention;
  }

  static isMention(maybeMention: any): maybeMention is Mention {
    return Inline.isInline(maybeMention) && maybeMention.type === Mention.TYPE;
  }
}
